<template>

  <div class="btn-group">
    <li
      @click="toggleMenu"
      class="dropdown-toggle"
      :class="{ 'dropdown-toggle-placeholder': !selectedOption.name }"
    >
      {{ selectedOption.name || placeholder }}
      <i class="i_s16 ico_selectArr"></i>
    </li>

    <ul class="compDropdown-menu" v-if="showMenu">
      <li v-for="(option, idx) in options" :key="idx">
        <a href="javascript:void(0)" @click="updateOption(option)">
        {{ option.name }}
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      selectedOption: this.selected,
      showMenu: false,
    }
  },
  props: {
    options: {
      type: Object,
      required: true,
      default() {
        return {};
      }
    },
    selected: {
      type: Object,
      default() {
        return {};
      }
    },
    placeholder: {
      type: String,
      default: "선택해주세요.",
    },
    closeOnOutsideClick: {
      type: Boolean,
      default: true,
    },
  },

  mounted() {
    this.selectedOption = this.selected;
    if (this.placeholder)
    {
      this.placeholderText = this.placeholder;
    }
    if (this.closeOnOutsideClick) {
      document.addEventListener('click', this.clickHandler);
    }
  },

  destroy() {
      document.removeEventListener('click', this.clickHandler);
  },

  methods: {
    updateOption(option) {
      this.selectedOption = option;
      this.showMenu = false;
      this.$emit('updateOption', this.selectedOption);
    },
    toggleMenu() {
      this.showMenu = !this.showMenu;
    },
    clickHandler(event) {
      const { target } = event;
      const { $el } = this;

      if (!$el.contains(target)) {
        this.showMenu = false;
      }      
    },
  
  }
}
</script>

<style lang="scss" scoped>
.btn-group {
  position: relative;
  display: inline-block;
  vertical-align: middle;
}
.btn-group a:hover {
  text-decoration: none;
}
.dropdown-toggle {
  min-width: 124px;
  max-width: 124px;
  padding: 0 30px 0 10px;
  height: 44px;
  line-height:44px;
  text-transform: none;
  font-weight: 300;
  background-size: 0 2px, 100% 1px;
  background-repeat: no-repeat;
  background-position: center bottom, center calc(100% - 1px);
  box-shadow: none;
  border-radius: 4px;
  box-sizing: border-box;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  background-color:#fff;
}
.dropdown-toggle:hover {
  cursor: pointer;
}
.compDropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1000;
  padding: 8px 0;
  margin-top:4px;
  text-align: left;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: var(--shadow);
  width:100%;
  box-sizing: border-box;
}
.compDropdown-menu > li > a {
  padding: 7px 16px;
  display: block;
  clear: both;
  white-space: nowrap;
  text-decoration: none;
}
.compDropdown-menu > li > a:hover {
  background: #f5f5f5;
}
.compDropdown-menu > li {
  overflow: hidden;
  width: 100%;
  position: relative;
  margin: 0;
}
.ico_selectArr {
  width: 16px;
  height:100%;
  position: absolute;
  top: 0;
  right: 10px;
  margin-left: -24px;
  background: url(@/assets/images/icon/ico_select-arr.svg) no-repeat center;  
}
</style>